tr:nth-child(even) {background-color: #ffffff;}
.nav {
  padding: 10px 30px;
  
  /*background-color:#313233b8;*/
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.react-confirm-alert-overlay{
  background: #132f4cba !important;
}
.react-confirm-alert-body{
  background-color: #0059b2 !important;
  color: #fff !important;
}
.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  font-size: 32px;
  color: #fff;
  /*padding: 10px 10px 10px 10px;*/
  font-family: 'Orbitron', sans-serif;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  float: right;
  font-size: 18px;
}

.nav > .nav-links > a {
  display: inline-block;
  padding: 0px 10px 0px 10px;
  text-decoration: none;
  color: #000000;
  font-weight: 600;
}

.nav > .nav-links > a:hover {
 /* background-color: rgba(0, 0, 0, 0.3);*/
 color: #007fff;
}

.nav > #nav-check {
  display: none;
}

@media (max-width:600px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    /*height: 50px;*/
   
    line-height: 60px;
    font-size: 32px;
  }
  .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
   color: #007fff;
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #eee;
  }
  .nav > .nav-links {
    position: absolute;
    display: block;
    width: 100%;
    background-color:#f3f6f9;
    color: #000000;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
    top: 50px;
    padding: 20px 10px;
    right: 0;
    border: 1px solid #e0e0e0;
    backdrop-filter: blur(10px);
  }
  .nav > .nav-links > a {
    display: block;
    width: 100%;
    margin-bottom: 20px;
  }
  .nav > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
    height: 0px;
    padding: 0px;
    border: none;
  }
  .nav > #nav-check:checked ~ .nav-links {
    max-width: 300px;
    height: auto;
    overflow-y: auto;
  }
}
